				</div>
				<div class="body"
				style="opacity: 0.3">
					<p>Description of the promoter strength prediction model:</p>
					<p>
						We developed transcriptional intensity prediction models using a synthetic promoter library with 112,955 sequences. The models include a transformer, a Convolutional Neural Network (CNN),
						and a Long Short-Term Memory (LSTM) framework. Each model's prediction results showed high correlation with experimental data, with coefficients of 0.87, 0.76, and 0.81, respectively.
					</p>
				</div>
				<div
					class="head">
					<img
						src="../../assets/images/图标4.png"
						alt="" />
					<span><i>De novo</i> Promoter Generation</span>
				</div>
				<div class="body">
					<p>Description of the <i>de novo</i> promoter generation model:</p>
					<p>
						We developed a transcriptional strength prediction model using a synthetic promoter library containing 112,955 sequences. Promoters were divided into 8 sub-libraries based on their log<sub>2</sub> strength 
						values, with ranges as follows: >10.5, 3.98&ndash;10.5, 1.51&ndash;3.98, 0.57&ndash;1.51, 0.217&ndash;0.57, 0.082&ndash;0.217, and &lt;0.031. Using these strength levels, we developed a conditional diffusion model to generate novel 
						promoter sequences with target strengths. Experimental validation showed a correlation of 0.93 between the predicted results and the actual results.
					</p>
					<p>
						Furthermore, we use the Transformer prediction model to predict the strength of sequences generated by the conditional diffusion model. Promoter sequences with target strength are obtained 
						through reverse filtering. Experimental validation shows that the correlation between the predicted results and the actual results is 0.95.
					</p>
