<script>
import axios from 'axios'
export default {
	data() {
		return {
			form: {
				project_name: '',
				strength_class: '',
				promoter_numbers: ''
			},
			fileName: '',
			top_sequences: []
		};
	},
	methods: {
		onsubmit() {
			const data = new URLSearchParams();
			// 表单验证
			if (!this.form.project_name || !this.form.target_strength || !this.form.promoter_numbers) {
				alert("Please complete the form!");
				return;
			}
			if (isNaN(this.form.target_strength) || isNaN(this.form.promoter_numbers)) {
				alert("Target strength and promoter numbers must be numeric!");
				return;
			}
			// 增加范围验证
			if (this.form.target_strength < 0 || this.form.target_strength > 100) {
				alert("Target strength must be between 0 and 100! ");
				return;
			}
			if (this.form.promoter_numbers < 1 || this.form.promoter_numbers > 100) {
				alert("Promoter numbers must be between 1 and 100!");
				return;
			}
			data.append('project_name', this.form.project_name);
			data.append('target_strength', this.form.target_strength);
			data.append('promoter_numbers', this.form.promoter_numbers);

			axios.post('http://139.129.26.28:80/submit', data).then(res => {
				console.log(res);
				this.fileName = res.data.filename;
				this.top_sequences = res.data.top_sequences.map(item => {
					return {
						sequence: item['Promoter Sequences'],
						strength: item['Promoter Strength'],
					};
				});
				alert('Submission successful!'); // 提示用户提交成功
				}).catch(error => {
					console.error('Submission failed:', error);
					alert('Submission failed. Please try again.'); // 提示用户提交失败
					});
				},
		downloadResult() {
			// window.open(`http://139.129.26.28:80/download/${this.fileName}`)
			const link = document.createElement('a')
			link.href = `http://139.129.26.28:80/download/${this.fileName}`
			link.download = this.fileName
			link.click()
		},
		toPage() {
			console.log('toPage')
			this.$router.push('/Promoter Strength Prediction');
		}
	}
}
</script>

